import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Card, Row, Table, Col, Input } from 'antd';
import { commentEndCourse } from 'dump/data';
import FormSerchCommentEndCourse from './FormSerchCommentEndCourse';

const columns = [
  {
    title: 'Mã SV',
    dataIndex: 'masv',
    key: 'name',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'nameEn',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'fromScores',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'toScores',
  },
  {
    title: 'Nhận xét',
    dataIndex: 'comment',
    render: (data, record) => (
      <Fragment>
        <Input.TextArea autosize defaultValue={record.comment} />
      </Fragment>
    ),
  },
];

class ListCommentEndCoursePage extends Component {
  state = {};

  render() {
    return (
      <Card title="Nhận xét cuối khóa">
        <Row>
          <Col span={24}>
            <FormSerchCommentEndCourse />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={commentEndCourse} columns={columns} />
          </Col>
          <Col span={24} />
          <Col offset={8} span={12}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={this.cancelUpload} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListCommentEndCoursePage />
  </Page>
);



